.calendarWrapper{
    width: 100%;
    height: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.calendarContent{
    width: 90%;
    height: 95%;
    overflow: hidden;
    background-color: white;
    padding: 10px;
    border-radius: 10px;
}

.eventModalBackground {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999; /* Ensure modal is on top */
  }
  
  .eventModal {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    z-index: 1000; /* Ensure modal is on top of the background */
  }
  
  .eventModalBackground::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 998; /* Ensure background is behind modal */
  }
  
  body.modal-open {
    overflow: hidden; /* Prevent scrolling on background */
  }
  
  body.modal-open * {
    pointer-events: none; /* Disable interactions with elements behind modal */
  }
  
  
  .eventModal h2 {
    margin-top: 0;
  }
  
  .formGroup {
    margin-bottom: 15px;
  }
  
  .formGroup label {
    display: block;
    margin-bottom: 5px;
  }
  
  .formGroup input,
  .formGroup textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    margin-left: 15px;
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  